
























































































































































































































































































































































/* icon-font */
@import 'assets/icon/g6_editor_font.css';

.flow-view {
  position: relative;
  width: 100%;
  height: calc(100% - 20px)!important;
  // 底部区域（左项目面板、中心画布、右属性面板）
  .bottom-container {
    width: 100%;
    height: 100%;
    display: flex;
    background: #f7f9fb;
    justify-content: space-around;
    // 中间画布
    .center-pannel {
      background: #ffffff;
      display: inline-block;
      width: 100%;
      height: 100%;
      border: 1px solid #dedbe2;
      // 流程图
      .flow {
        width: 100%;
        height: 100%;
        overflow: hidden;
      }
    }
  }
}

